.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.circle {
  display: flex;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border-style: dashed;
}

.links {
  float: right;
  justify-content: center;
  margin: auto 5% 0 10px;
  width: 30%;

  border-radius: 10%;
  border-style: dashed;
  position: relative;
  top: 20px;
}
ul.no-bullets {
  list-style-type: none; /* Remove bullets */
  padding: 0; /* Remove padding */
}

.money {
  justify-content: center;
  margin: auto 80% 0 10px;
  width: 30%;

  border-radius: 10%;
  border-style: dashed;
  position: sticky;
  top: 20px;
}
.box-value {
  padding-right: 10px;
  padding-left: 10px;
}

.you-won {
  border-color: green;
}
.you-lost {
  border-color: red;
}

.a-question {
}

.not-opened {
  background-color: gray;
}
.safety-opened {
  background-color: lightgrey;
}
.a-truth {
  background-color: green;
}
.a-lie {
  background-color: red;
}
.a-truth-reveal {
  background-color: lightgreen;
}
.a-lie-reveal {
  background-color: pink;
}
.last-10-seconds {
  color: red;
}
.text {
  margin: auto;
}

.row {
  display: flex;
  justify-content: center;
}

button.button1{
  display:inline-block;
  padding:0.35em 1.2em;
  border:0.1em solid #FFFFFF;
  margin:0 0.3em 0.3em 0;
  border-radius:0.12em;
  box-sizing: border-box;
  text-decoration:none;
  font-family:'Roboto',sans-serif;
  font-weight:300;
  color:#000000;
  text-align:center;
  transition: all 0.2s;
}
button.button1:hover{
  color:#000000;
  background-color:lightgrey;
}
@media all and (max-width:30em){
  a.button1{
    display:block;
    margin:0.4em auto;
    }
}